import {
  Table,
  TableBody,
  TableCell,
  TableHeader,
  TableRow,
} from "@windmill/react-ui";
import React, { useEffect, useRef, useState } from "react";
import { FiPrinter } from "react-icons/fi";
import { Link, useParams } from "react-router-dom";
import ReactToPrint from "react-to-print";
import { getInvoiceById } from "services/Api";
import INvoice from "./InvoiceDesign"
import PageTitle from "components/Typography/pageTitleButton";

// import { Col, Container, Image, Row, Table } from "react-bootstrap";

const InvoiceDesign = ({ }) => {
  const { id } = useParams();
  const printRef = useRef();
  const [invoiceData, setInvoiceData] = useState([])
  const getSingleInvoice = async (id) => {
    const invoice = await getInvoiceById(id);
    setInvoiceData(invoice?.data?.data);
  };

  useEffect(() => {
    if (id) {
      getSingleInvoice(id);
    }
  }, [id]);
  const resetBtn = () => {
    console.log("fsefh")
    window.history.go(-1);
  }
  return (<>
      <PageTitle resetBtn={resetBtn}></PageTitle>
        <INvoice  invoiceData={invoiceData} ref1={printRef}/>
       <div className="p-5">
        <ReactToPrint
            trigger={() => (
              <button className="flex items-center text-sm leading-5 transition-colors duration-150 font-medium focus:outline-none px-5 py-2 rounded-md text-white bg-green-500 border border-transparent active:bg-green-600 hover:bg-green-600 focus:ring focus:ring-purple-300 w-auto">
                {("PrintInvoice")}
                <span className="ml-2">
                  <FiPrinter />
                </span>
              </button>
            )}
            content={() => printRef.current}
            documentTitle="Invoice"
          />
        </div>
    </>);
};

export default InvoiceDesign;
