import { Button } from '@windmill/react-ui'
import React from 'react'
import { FiArrowLeft } from "react-icons/fi";

const PageTitle = ({ children,resetBtn }) => {
  return (<>
  <div style={{display:"flex",alignItems:"center"}}>
   <Button icon={FiArrowLeft} onClick={() => resetBtn()} aria-label="Like" style={{width:"40px"}}/>
    <h1 className="my-6 text-lg font-bold text-gray-700 dark:text-gray-300 ml-3">{children}</h1>
    </div> </> )
}

export default PageTitle
